<template>
  <div id="practice" v-cloak v-loading="loading2"
       element-loading-text="Loading...">
    <div class="list-chapter" v-if="studyList.length">
      <ul class="chapter-ul">
        <li class="chapter-ul-li" v-for="(item,index) in studyList" :key="index">
          <div class="chapter-ul padd-l-15 padd-r-15">
            <div class="font15 audio-h2 flex-container flex-jus-s padd-b-5 text-left font-weight" v-html="item.title"></div>
            <!--<audio :src="item.media_path" controls="controls"></audio>-->
            <div class="study-video" style="width: 100%;">
              <!--<audio :src="item.media_path" controls></audio>-->
              <!--<VueAudio :theUrl="audios[index].url" :theControlList="audios[index].controlList"/>-->
              <aplayer autoplay :music="{
                title: item.content,
                author: 'a',
                url: item.media_path,
                pic: item.media_path,
              }">
              </aplayer>
            </div>
            <div class="practice-bg text-left font14" @click="spreadFun(item.content, $event)" v-show="item.content.length !=0">
              {{item.content }}
              <!--<img :style="{ width: '1rem',float: 'right',position:'absolute',right:'5px',top:'4px'}" :src="item.content.length > 60 ? uarr : ''"/>-->
            </div>
          </div>
          <div class="audio-title">
            <!--<h2 class="font15 audio-h2 flex-container flex-jus-s">我的:</h2>-->
            <div class="practice-img flex-container flex-jus-c flex-warp ">
              <div class="practice-yuyin">
                <div class="videoBg" @touchstart.prevent="TouchStart($event, index)"
                     @touchend.prevent="TouchEnd($event, index)"
                     v-show="item.record == 1"></div>
                <!-- <img :src="Yinp"  @touchstart.prevent="TouchStart($event)" @touchend.prevent="TouchEnd($event)"/> -->
              </div>
              <!--<div class="font14 color-99 padd-t-10">大声朗读，不断提升</div>-->
              <div class="shiting" v-show="item.record == 0">
                <van-row>
                  <van-col span="12">
                    <div @click="PlayClick(index)">
                      <img src="@/assets/images/icon_shiting.png" alt="">
                      <p>试听</p>
                    </div>
                  </van-col>
                  <van-col span="12">
                    <div @click="ClearVideo(index)">
                      <img src="@/assets/images/chonglu.png" alt="">
                      <p>重录</p>
                    </div>
                  </van-col>
                </van-row>
              </div>
            </div>
          </div>
        </li>
        <!--<li class="chapter-ul-li">-->
        <!--<div class="chapter-ul padd-l-15 padd-r-15">-->
        <!--<h2 class="font15 audio-h2 flex-container flex-jus-s padd-b-5">练习一:</h2>-->
        <!--<audio src="http://sc1.111ttt.cn/2018/1/03/13/396131227447.mp3" controls="controls"></audio>-->
        <!--<div class="practice-bg text-justify font14"> But if you wanna cry. Cry on my shoulder.</div>-->
        <!--</div>-->
        <!--<div class="audio-title">-->
        <!--<h2 class="font15 audio-h2 flex-container flex-jus-s">我的:</h2>-->
        <!--<div class="practice-img flex-container flex-jus-sa  padd-t-15">-->
        <!--<button class="practice-listen flex-container flex-jus-c align-items-cen"><i class="iconfont1 icon-listen"></i>试听</button>-->
        <!--<button class="practice-listen flex-container flex-jus-c align-items-cen"><i class="iconfont1 icon-retake"></i>重录</button>-->
        <!--</div>-->
        <!--</div>-->
        <!--</li>-->
      </ul>

    </div>
    <div class=" flex-container flex-jus-c align-items flex-direction-col height100vh" v-if="studyList.length==0">
      <div class="homeImg"><img :src="zwt"/></div>
      <p class="text-center font15 color-99 padd-t-20">暂时还没有相关信息哦~</p>
    </div>


    <van-tabbar v-model="active">
      <van-tabbar-item @click="geSt">
        <span>学习</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active : icon.normal"
        >
      </van-tabbar-item>
      <van-tabbar-item><span>练习</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active1 : icon.normal1"
        >
      </van-tabbar-item>
      <van-tabbar-item @click="goTe" v-show="testList"><span>测验</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active2 : icon.normal2"
        >
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
  import VueAudio from '@/components/VueAudio'
  import avatar from '@/assets/images/banner1.jpg'
  import Yinp from '@/assets/images/yuyin.png'
  import study from '@/assets/images/xuexi_1.png'
  import study_active from '@/assets/images/xuexi.png'
  import practice from '@/assets/images/lianxi_1.png'
  import practice_active from '@/assets/images/lainxi.png'
  import test from '@/assets/images/ceyan_1.png'
  import test_active from '@/assets/images/ceyan.png'
  import zwt from '@/assets/images/wx.png'
  import uarr from '@/assets/images/uarr.png'
  import darr from '@/assets/images/darr.png'
  import Vue from 'vue'
  import Aplayer from 'vue-aplayer'
  import {Tabbar, TabbarItem, Toast} from 'vant'

  Vue.use(Toast);
  const jsApi = [
    "checkJsApi",
    "onMenuShareTimeline",
    "onMenuShareAppMessage",
    "onMenuShareQQ",
    "onMenuShareWeibo",
    "onMenuShareQZone",
    "hideMenuItems",
    "showMenuItems",
    "hideAllNonBaseMenuItem",
    "showAllNonBaseMenuItem",
    "translateVoice",
    "startRecord",
    "stopRecord",
    "onVoiceRecordEnd",
    "playVoice",
    "onVoicePlayEnd",
    "pauseVoice",
    "stopVoice",
    "uploadVoice",
    "downloadVoice",
    "chooseImage",
    "previewImage",
    "uploadImage",
    "downloadImage",
    "getNetworkType",
    "openLocation",
    "getLocation",
    "hideOptionMenu",
    "showOptionMenu",
    "closeWindow",
    "scanQRCode",
    "chooseWXPay",
    "openProductSpecificView",
    "addCard",
    "chooseCard",
    "openCard"
  ];
  Vue.use(Tabbar).use(TabbarItem).use(Toast);
  export default {
    name: 'practice',
    components: {
      Aplayer
    },
    data() {
      return {
        active: 1,
        avatar: avatar,
        Yinp: Yinp,//语音图片
        icon: {
          normal: study,
          active: study_active,//自定义图标1
          normal1: practice,
          active1: practice_active,//自定义图标2
          normal2: test,
          active2: test_active,//自定义图标3
        },
        studyList: [],
        zwt: zwt,
        START: 0,
        spreadStatus: 0,
        uarr: uarr,
        darr: darr,
        audios: [],
        loading2:true,
        list3: [],
        testList: false
      }


    },
    mounted() {
    },
    created() {
      this.getSid();
      this.getTest();
      this.getWx();
      document.oncontextmenu = function (e) {
        e.preventDefault();
      };
    },
    methods: {
      spreadFun(text, event) {
        var self = this;
        if (text.length > 18) {
          if (self.spreadStatus == 0) {
            event.currentTarget.innerHTML = text + ' <img style="width: 1rem;float: right;" src="' + self.darr + '"/>';
            self.spreadStatus = 1;
          } else if (self.spreadStatus == 1) {
            event.currentTarget.innerHTML = text.substr(0, 18) + ' <img style="width: 1rem;float: right;" src="' + self.uarr + '"/>';
            self.spreadStatus = 0;
          }
        }
      },
      getWx() {
        var self = this;
        self.http.get("/api/v1/Common/generate_config?curUrl=" + location.href.split("#")[0])
          .then(res => {
            const msg = res.data.data;
            if (res.data.code == 200) {
              if (msg) {
                self.wx.config({
                  debug: false,
                  appId: msg.appId,
                  timestamp: msg.timestamp,
                  nonceStr: msg.nonceStr,
                  signature: msg.signature,
                  jsApiList: jsApi
                });
                self.wx.ready(function () {
                  self.wx.checkJsApi({
                    jsApiList: jsApi,
                    success: function (res) {
                      // console.log(JSON.stringify(res));
                    }
                  });
                });
                self.wx.error(function (err) {
                  Toast("配置失败：" + JSON.stringify(err));
                });
              } else {
                Toast("无数据！")
              }
            } else {
              if (res.data.code == -100) {
                localStorage.setItem("login_token", "");
                setTimeout(function() {
                  window.location.href =
                    "http://weikt.apps.skylarkly.com/api/v1/Login/user_login";
                  // window.location.href = "http://weikt.webuildus.com/api/v1/Login/user_login";
                }, 300);
              } else {
                Toast(res.data.msg);
              }
            }
          });
      },
      TouchStart(e, index) {
        // Toast("录音开始");
        e.preventDefault();
        var self = this;
        self.wx.ready(function () {
          self.wx.startRecord({
            success: function () {
              Toast("录音开始");
              self.START = new Date().getTime();
              self.wx.onVoiceRecordEnd({
                // 录音时间超过一分钟没有停止的时候会执行 complete 回调
                complete: function (res) {
                  Toast("最多只能录制一分钟");
                  self.studyList[index].localId = res.localId;
                  self.updateStudyListRecord(1, index);
                  // self.uploadluyin(self.localId);
                }
              });
            },
            fail: function (err) {
              Toast("录音结束:" + JSON.stringify(err));
            },
            cancel: function () {
              Toast("用户拒绝授权录音");
            }
          });
        });
      },
      TouchEnd(e, index) {
        e.preventDefault();
        var self = this;
        let END = new Date().getTime();
        //录音时间
        let luyintime = END - this.START;
        if (luyintime < 2000) {
          END = 0;
          this.START = 0;
          self.wx.stopRecord({});
          Toast("录音时间不能少于2秒");
          return false;
          //小于300ms，不录音
        } else {
          self.wx.stopRecord({
            success: function (res) {
              Toast("结束录音");
              self.studyList[index].localId = res.localId;
              self.updateStudyListRecord(0, index);
              // 上传到服务器
              // self.uploadluyin(self.localId);
            },
            fail: function (res) {
              Toast(JSON.stringify(res));
            }
          });
        }
      },
      ClearVideo(index) {
        this.studyList[index].localId = "";
        Toast("录音已经清空，请重新录制。");
        this.updateStudyListRecord(1, index);
      },
      PlayClick(index) {
        var self = this;
        self.wx.ready(function () {
          if (self.studyList[index].localId == "") {
            Toast("请先录制一段声音");
            return;
          }
          self.wx.playVoice({
            localId: self.studyList[index].localId
          });
        });
      },
      getSid() {
        var self = this;
        this.http.post('/api/v1/Course/get_chapter_exercise', {id: this.$route.query.id}).then((res) => {
          if (res.data.code == 200) {
            if (!res.data.data || res.data.data.length == 0) {
              Toast("无练习题数据！");
            } else {
              self.studyList = res.data.data;
              for (let i = 0; i < self.studyList.length; i++) {
                self.studyList[i].record = 1;
                self.studyList[i].localId = "";
              }
              for (var i = 0; i < self.studyList.length; i++) {
                var json = {
                  url: self.studyList[i].media_path,
                  controlList:''
                };
                self.audios.push(json);
              }
            }
            self.studyList = res.data.data;
            for (let i = 0; i < self.studyList.length; i++) {
              self.studyList[i].record = 1;
              self.studyList[i].localId = "";
            }
          } else {
            if (res.data.code == -100) {
              localStorage.setItem("login_token", "");
              setTimeout(function() {
                window.location.href =
                  "http://weikt.apps.skylarkly.com/api/v1/Login/user_login";
                // window.location.href = "http://weikt.webuildus.com/api/v1/Login/user_login";
              }, 300);
            } else {
              Toast(res.data.msg);
            }
          }
          this.loading2=false;
        })
      },
      geSt() {
        this.$router.push({
          path: 'study',
          query: {id: this.$route.query.id},

        })
      },
      goTe() {
        this.$router.push({
          path: 'test',
          query: {id: this.$route.query.id},
        })
      },
      getTest() {
        var self = this;
        this.http.post("/api/v1/Course/get_chapter_test", {
          id: self.$route.query.id
        }).then(res => {
          if (res.data.code == 200) {
            if (!res.data.data || res.data.data.length == 0) {
              // Toast("无测验题数据！");
            } else {
              self.testList = res.data.data.list.length == 0 ? false : true;
            }
          } else {
            // Toast(res.data.msg);
          }
        });
      },
      updateStudyListRecord(statu, index) {
        var self = this;
        self.studyList[index].record = statu;
        let list = self.studyList[index];
        self.studyList.splice(index, 1, list);
      }
    }
  };
</script>

<style lang="stylus" type="text/stylus">
  @import "practice.styl";
</style>
<style lang="less" type="text/less" scoped>
  .videoBg {
    margin: 0 auto;
    width:24px;
    height: 24px;
    background: url('../../assets/images/yuyin.png') center no-repeat;
    background-size:cover;
  }

  .shiting {
    width: 50%;
    margin: 0 auto;
    padding: 0.5rem 25% 0 25%;
    img {
      width: 0.98rem;
      height: 0.9rem;
    }
    p {
      font-size: 0.75rem;
      font-weight: normal;
      font-stretch: normal;
      letter-spacing: 0rem;
      color: #4fdaa9;
    }
  }

  .spread {
    width: 1rem;
    height: 1rem;
  }
</style>



