<template>
  <div id="collection" v-cloak v-loading="loading2"
       element-loading-text="Loading...">
    <ul class="collection" v-waterfall-lower="loadMore"
        waterfall-disabled="disabled"
        waterfall-offset="400">
      <li class="card" v-for="(i,k) in list" :key="k" @click="listDetail($event, k)">
        <div class="fl">
          <h2>{{i.title}}</h2>
          <!-- <p>英语学习  |  3节课</p> -->
        </div>
        <div class="fr">
          <div class="button" @click="cancelCol(i.curriculum_id)">
            <span>取消收藏</span>
          </div>
        </div>
        <div class="clear"></div>
      </li>

    </ul>
    <div v-if="list.length==0" class=" flex-container flex-jus-c align-items flex-direction-col height100vh">
      <div class="homeImg"><img :src="zwt"/></div>
      <p class="text-center font15 color-99">暂时还没有相关信息哦~</p>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import { Waterfall, Toast } from "vant";
import zwt from "@/assets/images/wx.png";
Vue.use(Waterfall);
Vue.use(Toast);
export default {
  directives: {
    WaterfallLower: Waterfall("lower"),
    WaterfallUpper: Waterfall("upper")
  },
  name:'collection',
  data() {
    return {
      disabled: false,
      list: "",
      zwt: zwt,
      page: 1,
      loading2:true,
      num: 10
    };
  },
  components: {},
  created() {},
  computed: {},
  mounted() {
    this.loadMore();
  },
  methods: {
    loadMore() {
      var self = this;
      self.http
        .post("/api/v1/User/get_collect_list", {
          intl: 10,
          p: self.page
        })
        .then(res => {
          if (res.data.code == 200) {
            self.disabled = true;
            if (!res.data.data || res.data.data.length == 0) {
              Toast("暂无数据！");
              return;
            }
            setTimeout(() => {
              if (res.data.data.length == 10) {
                self.disabled = false;
              }
              self.list = res.data.data;
            }, 200);
          } else {
            if (res.data.code == -100) {
              localStorage.setItem("login_token", "");
              setTimeout(function() {
                window.location.href =
                  "http://weikt.apps.skylarkly.com/api/v1/Login/user_login";
                // window.location.href = "http://weikt.webuildus.com/api/v1/Login/user_login";
              }, 300);
            } else {
              Toast(res.data.msg);
            }
          }
          this.loading2=false;
        });
    },
    //取消收藏
    cancelCol(id) {
      this.http
        .post("/api/v1/Course/collect_course", { c_id: id })
        .then(res => {
          if (res.data.code == 200) {
            this.page = 1;
            this.loadMore();
          }
        });
    },
    listDetail(e, index) {
      let _this = this;
      this.$router.push({
        path: "listdetail",
        query: { id: _this.list[index].curriculum_id }
      });
    }
  }
};
</script>
<style lang="less" type="text/less" scoped>
* {
  box-sizing: border-box;
}

.collection {
  padding: 1.05rem 0.6rem;
}

.card {
  margin-bottom: 0.6rem;
  padding: 1.02rem 0.8rem;
  width: 100%;

  background-color: #ffffff;
  box-shadow: 0rem 0.25rem 0.5rem 0rem rgba(84, 218, 197, 0.1);
  border-radius: 0.5rem;
  h2 {
    font-family: PingFang-SC-Bold;
    font-size: 0.75rem;
    font-weight: normal;
    font-stretch: normal;
    letter-spacing: 0rem;
    color: #333333;
  }

  p {
    font-family: PingFang-SC-Medium;
    font-size: 0.6rem;
    font-weight: normal;
    font-stretch: normal;
    letter-spacing: 0rem;
    color: #999999;
    margin-top: 0.68rem;
  }
  .button {
    margin-top: 0.5rem;
    border: solid 1px #4fdaa9;
    width: 3.5rem;
    height: 1.5rem;
    border-radius: 0.25rem;
    padding: 0;
    span {
      font-family: PingFang-SC-Medium;
      font-size: 0.6rem;
      font-weight: normal;
      font-stretch: normal;
      letter-spacing: 0rem;
      color: #4fdaa9;
    }
  }
}
</style>



