<template>
  <div id="studyaudio" v-cloak v-loading="loading2"
       element-loading-text="Loading...">
    <div class="list-chapter">
      <ul class="chapter-ul">
        <li class="chapter-ul-li">
          <!--<audio :src="studyList.media_path" controls="controls"></audio>-->
          <audio :src="studyList.media_path" id="audio" @ended="overAudio"></audio>
          <!-- 播放 -->
          <div class="play" v-if="audioPlayShow" @click="playAudio">
            <img :src="audioImg">
          </div>
          <!-- 暂停-->
          <div class="play" v-else>
            <img :src="audioImg" @click="pauseAudio">
          </div>
          <div class="audio-box">
            <div class="line-wrapper" ref="line">
              <div class="line-bg" :style="{width: audioWidth + 'px'}"></div>
            </div>
          </div>
          <div class="audio-title">
              <h2 class="font15 audio-h2 flex-container flex-jus-s">正文</h2>
              <article class="audio-art text-justify font14 padd-t-10">
               {{studyList.content}}
              </article>
            <div class="audio-img padd-t-10">
              <video  webkit-playsinline="true" x-webkit-airplay="true"  playsinline="true"x5-video-player-type="h5"x5-video-player-fullscreen="true"width="100%" height="100%"preload="auto" controls="controls">
                <source :src="studyList.media_path" type="video/mp4" />
                <source :src="studyList.media_path" type="video/webM" />
                <source :src="studyList.media_path" type="video/ogg" />
              </video>
            </div>
          </div>
        </li>
        <li class="chapter-ul-li">
          <h2 class="font15 audio-h2 flex-container flex-jus-s  padd-l-15 padd-r-15">生词:</h2>
          <div class="audio-img  padd-l-15 padd-r-15 flex-container flex-jus-s flex-direction-col align-items-start font14">
            <p>shoulder : n. 肩膀</p>
            <p> memories ：n. 记忆</p>
          </div>
        </li>
      </ul>

    </div>

    <van-tabbar v-model="active">
      <van-tabbar-item>
        <span>学习</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active : icon.normal"
        >
      </van-tabbar-item>
      <van-tabbar-item to="practice"><span>练习</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active1 : icon.normal1"
        >
      </van-tabbar-item>
      <van-tabbar-item to="test"><span>测验</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active2 : icon.normal2"
        >
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import avatar from "@/assets/images/banner1.jpg";
import study from "@/assets/images/xuexi_1.png";
import study_active from "@/assets/images/xuexi.png";
import practice from "@/assets/images/lianxi_1.png";
import practice_active from "@/assets/images/lainxi.png";
import test from "@/assets/images/ceyan_1.png";
import test_active from "@/assets/images/ceyan.png";
import audioImg from "@/assets/images/zanting_1.png";
import Vue from "vue";
import { Tabbar, TabbarItem, Toast } from "vant";
Vue.use(Tabbar)
  .use(TabbarItem)
  .use(Toast);
export default {
  name: "studyaudio",
  data() {
    return {
      active: 0,
      avatar: avatar,
      audioImg:audioImg,
      loading2:true,
      icon: {
        normal: study,
        active: study_active, //自定义图标1
        normal1: practice,
        active1: practice_active, //自定义图标2
        normal2: test,
        active2: test_active //自定义图标3
      },
      studyList: []
    };
  },
  mounted() {
    this.getSid();
  },
  methods: {
    getSid() {
      var self = this;
      this.http
        .post("/api/v1/Course/get_chapter_info", { id: self.$route.query.id })
        .then(res => {
          if (res.data.code == 200) {
            if (!res.data.data || res.data.data.length == 0) {
              Toast("暂无数据！");
              return;
            }
            self.studyList = res.data.data;
          } else {
            if (res.data.code == -100) {
              localStorage.setItem("login_token", "");
              setTimeout(function() {
                window.location.href =
                  "http://weikt.apps.skylarkly.com/api/v1/Login/user_login";
                // window.location.href = "http://weikt.webuildus.com/api/v1/Login/user_login";
              }, 300);
            } else {
              Toast(res.data.msg);
            }
          }
          this.loading2=false;
        });
    },
    /**
     * 播放音频
     */
    playAudio() {
      this.audioPlayShow = false
      let audio = this.audio = document.getElementById('audio')
      /**
       * this.$refs.line.offsetWidth 是容器的宽度
       * audio.duration 是音乐的播放时间长度
       */
      let step = this.toFixed(this.$refs.line.offsetWidth / audio.duration, 1)
      if (this.audioIsOver) {
        this.audioIsOver = false
        this.audioWidth = 0
        audio.load()
      }
      this.audioInterval = setInterval(() => {
        this.audioWidth = this.add(this.audioWidth, step)
      }, 1000)
      audio.play()
    },
    /**
     * 暂停音频
     */
    pauseAudio() {
      this.audioPlayShow = true
      let audio = document.getElementById('audio')
      audio.pause()
      // 清除定时器
      clearInterval(this.audioInterval)
    },
    /**
     * 音频播放完的时候
     */
    overAudio() {
      this.audioWidth = this.$refs.line.offsetWidth
      this.audioIsOver = true
      this.pauseAudio()
    },
  }
};
</script>

<style lang="stylus" type="text/stylus">
@import 'studyaudio.styl';
</style>



