import axios from 'axios'
import qs from 'qs'
import Vue from 'vue'
import Vuex from 'vuex'
let localhosts = 'http://weikt.apps.skylarkly.com/';  // 正式服

//let localhosts = 'http://weikt.webuildus.com/';
axios.interceptors.request.use(config => {
  // loading
  // console.log(config)
  // config.headers['X-mart-shop-version']='1.0.0'
  return config
}, error => {

  return Promise.reject(error)
});

axios.interceptors.response.use(response => {
  return response
}, error => {

//   Vue.prototype.$dialog.toast({
//     mes:'网络错误'
//   })
  // return Promise.resolve(error.response)
});
function checkStatus(response) {

  // loading
  // 如果http状态码正常，则直接返回数据
  if (response && (response.status === 200 || response.status === 304 || response.status === 400)) {
   if(response.data.status!=0){

    if(response.data.status==401){
      localStorage.removeItem('login_token')

    //   Vue.prototype.$dialog.toast({
    //     mes:'登陆已过期'
    //   })
    //   Vue.$router.replace('/login')
      return false
    }


   }

    return response
    // 如果不需要除了data之外的数据，可以直接 return response.data

  }
  return {
    status: 404,
    msg: '网络异常'
  }
}

function checkCode(res) {
  // 如果code异常(这里已经包括网络错误，服务器错误，后端抛出的错误)，可以弹出一个错误提示，告诉用户
  if (res.status === 404) {
    // alert(res.msg)


  }
  if (res.data && (!res.data.success)) {
    // alert(res.data.error_msg)
  }
  return res
}
export default {
  post (url, data) {
    return axios({
      method: 'post',
      baseURL: localhosts,
      url,
      data: qs.stringify(data),
      timeout: 10000,
      headers: {
        'x-auth-info':localStorage.getItem('login_token')?localStorage.getItem('login_token'):''
      }
    }).then(
      (response) => {

        return checkStatus(response)
      }
    ).then(
      (res) => {
        return checkCode(res)

      }
    )
  },
    get (url, params) {
    return axios({
      method: 'get',
      baseURL: localhosts,
      url,
      params, // get 请求时带的参数
      timeout: 10000,
      headers: {  'x-auth-info':localStorage.getItem('login_token')?localStorage.getItem('login_token'):'' }
    }).then(
      (response) => {


        return checkStatus(response)
      }
    ).then(
      (res) => {

        return checkCode(res)
      }
    )
  },
  host:localhosts
}



