import Vue from 'vue'
import Router from 'vue-router'
import index from '@/views/index/index'
import homeList from '@/views/homeList/homeList'
import homeList2 from '@/views/homeList/homeList2'
import classify from '@/views/homeList/classify'
import listdetail from '@/views/listdetail/listdetail'
import study from '@/views/study/study'
import studyaudio from '@/views/studyaudio/studyaudio'
import practice from '@/views/practice/practice'
import test from '@/views/test/test'
import comment from '@/views/comment'
import mine from '@/views/mine'
import reading from '@/views/reading'
import rank from '@/views/rank'
import collection from '@/views/collection'
import task from '@/views/task'
Vue.use(Router)

export default new Router({
  routes: [
    {
      //首页
      path: '/',
      name: 'index',
      meta:{title:'微课堂-华侨学院'},

      component: index
    },
    //首页分类列表
    {
      path: '/homeList',
      name: 'homeList',
      meta:{
      title:'学英语',
      },
      component: homeList
    },{
      path: '/classify',
      name: 'classify',
      meta:{
      title:'列表分类',
      },
      component: classify
    },
    ,{
      path: '/homeList2',
      name: 'homeList2',
      meta:{
      title:'学英语',
      },
      component: homeList2
    },
    //分类详情
    {
      path: '/listdetail',
      name: 'listdetail',
      component: listdetail
    },
    //学习-视频
    {
      path: '/study',
      name: 'study',
      component: study
    },
    //学习-音频
    {
      path: '/studyaudio',
      name: 'studyaudio',
      component: studyaudio
    },
    //练习
    {
      path: '/practice',
      name: 'practice',
      component: practice
    },
    //测验
    {
      path: '/test',
      name: 'test',
      component: test
    },
    {
      path: '/comment',
      name: 'comment',
      component: comment,
      meta: {
        title: '作业点评'
      }

    },
    {
      path: '/mine',
      name: 'mine',
      component: mine,
      meta: {
        title: '个人中心'
      }

    },
    {
      path: '/reading',
      name: 'reading',
      component: reading,
    },
    {
      path: '/rank',
      name: 'rank',
      component: rank,
      meta: {
        title: '排行榜'
      }
    },
    {
      path: '/collection',
      name: 'collection',
      component: collection,
      meta: {
        title: '我的收藏'
      }
    },
    {
      path: '/task',
      name: 'task',
      component: task,
      meta: {
        title: '我的作业'
      }
    },
  ]
})



