  import paly from "@/assets/images/pause.jpg";
  import Vue from "vue";
  import {Toast, Dialog,Icon} from "vant";
  import VueResource from "vue-resource";
  let Base64 = require('js-base64').Base64;
  Vue.use(Dialog,Icon,Toast,VueResource)
  export default {
    name:'mine',
    data() {
      return {
        data: {
          loading2: true,
          paly:paly,
          userinfo: ""
        },
        demoEvents: []
      };
    },
    components: {},
    created() {

    },
    watch:{
      getUs(){
        let login_token = localStorage.getItem("login_token");
        login_token = this.$route.query["login_token"];
        localStorage.setItem("login_token", login_token);
        // if( !localStorage.getItem("login_token")){
        //   window.location.href = "https://skylarkly.com/unbind";
        // }
        if (login_token == "" || login_token == "undefined" || !login_token || login_token == "null") {

          if (login_token == "" || login_token == "undefined" || !login_token || login_token == "null") {
            // let url=Base64.encode("http://weikt.apps.skylarkly.com/view/index.html#/mine")
           let url=Base64.encode("http://weikt.webuildus.com/view/index.html#/mine")
            // window.location.href = "http://weikt.apps.skylarkly.com/api/v1/Login/user_login?return_url=http://weikt.apps.skylarkly.com/view/index.html#/mine";
            //    window.location.href = "http://weikt.apps.skylarkly.com/api/v1/Login/user_login?return_url="+url+"";
            window.location.href = "http://weikt.apps.skylarkly.com/api/v1/Login/user_login?return_url="+url+"";
          }
        }
      }

    },
    computed: {

    },
    mounted() {
      this.getdata();
    },
    methods: {
      getdata() {
        let self = this;
        let login_token = localStorage.getItem("login_token");
        login_token = this.$route.query["login_token"];
        localStorage.setItem("login_token", login_token);
        self.http.get("/api/v1/User/user_center").then(res => {
          if (res.data.code == 200) {
            self.data = res.data.data;
            if (!self.data) {
              Toast("无用户信息！");

              return;
            }
            for (var i = 0; i < self.data.study_times.length; i++) {
              var json = {
                date: self.data.study_times[i],
                title:''
              };
              self.demoEvents.push(json);

            }
            console.log(self.demoEvents);
          } if(res.data.code== -100) {
            Toast(res.data.msg);
            if (login_token == "" || login_token == "undefined" || !login_token || login_token == "null") {

              if (login_token == "" || login_token == "undefined" || !login_token || login_token == "null") {
               // let url=Base64.encode("http://weikt.apps.skylarkly.com/view/index.html#/mine")
                let url=Base64.encode("http://weikt.webuildus.com/view/index.html#/mine")
                  window.location.href = "http://weikt.webuildus.com/api/v1/Login/user_login?return_url="+url+"";
              }
            }
          }else{
            Toast(res.data.msg);
          }
          this.loading2=false;
        });
      },
