        var self = this;
        console.log(self);
        console.log(this.http);
        // window.location.href =
        //   "https://skylarkly.com/namespaces/200/dashboard/me";
        Dialog.confirm({title: '提示', message: '是否确认退出！'}).then(() => {
          // on confirm
          // window.location.href = "https://skylarkly.com/unbind";
          // localStorage.removeItem("login_token");
          console.log('111');
          localStorage.clear();
          //window.location.href = "https://skylarkly.com/unbind";
         // window.location.href ="https://skylarkly.com/api/v1/Login/user_login";

           window.location.href = "https://skylarkly.com/unbind?return_to=http://weikt.apps.skylarkly.com/view/index.html#/mine";
          // self.http.get('https://skylarkly.com/unbind', {
          //   return_to: "https://skylarkly.com/namespaces/200/user/bind",
          //   _ns_id: "200"
          // }).then(function (res) {
          //   localStorage.clear();
          //   console.log(res);
          // }).catch((err)=>{
          //   console.log(err)
          // })
        }).catch(() => {
          // on cancel
        });
      },
      myMessage() {
        window.location.href = "https://skylarkly.com/namespaces/200/receipts";
      }
      ,
      myApproval() {
        window.location.href =
          "https://skylarkly.com/namespaces/200/yet_another_workflow/flows";
      },


