    position: relative;
    top: 0.2rem;
    margin-right: 0.8rem;
  }

  .width93 {
    width: 0.9rem;
    height: 0.83rem;
    position: relative;
    top: 0.2rem;
    margin-right: 0.8rem;
  }

  .tab {
