    margin-top: 0.83rem;
  }

  .van-cell-group {
    border-radius: 10px;
    overflow: hidden;
    text-align: left;
  }

  .van-cell:not(:last-child)::after,
  .van-cell:not(:last-child)::after {
    left: 0;
  }


  .van-cell:not(:last-child):after{
    left: -50%;
  }

</style>



