<template>
  <div id="homeList" v-cloak v-loading="loading2"
       element-loading-text="Loading...">
    <div class="home-list flex-container flex-jus-s align-items-start flex-direction-col color-ff"
         v-for="(item,index) in List" :key="index" @click="golist(item.id)" v-waterfall-lower="loadMore"
         waterfall-disabled="disabled" waterfall-offset="400">
      <img :src="item.back_img ? item.back_img : avatar" alt="#">
      <div class="font24 text-left" v-html="item.title"></div>
      <div class="font14 padd-t-10 padding-b"><span class="home-line" v-html="item.classify_name"></span> <span
        class="padd-l-5">{{item.chapter_num}}章节</span></div>
    </div>

    <div v-if="ListShow"   class=" flex-container flex-jus-c align-items flex-direction-col height100vh">
      <div class="homeImg"><img :src="zwt"/></div>
      <p class="text-center font15 color-99">暂时还没有相关信息哦~</p>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import {Waterfall, Toast} from 'vant';
  Vue.use(Waterfall).use(Toast);
  import avatar from '@/assets/images/cha.png'
  import zwt from '@/assets/images/wx.png'
  // import jiu from '@/assets/images/jiu.png'
  // import jieri from '@/assets/images/jieri.png'
  export default {
    directives: {
      WaterfallLower: Waterfall('lower'),
      WaterfallUpper: Waterfall('upper')
    },
    name: 'homeList',

    data() {
      return {
        loading2: true,
        avatar: avatar,
        zwt: zwt,
        disabled: false,
        page: 1,
        ListShow:false,
        num: 10,
        List: []
      }
    },
    mounted() {
      this.loadMore()
    },
    methods: {
      loadMore() {
        var self = this;
        this.http.post('/api/v1/Course/get_course_list', {
          p: this.page, l: 10, t: this.$route.query.id
        }).then((res) => {
          if (res.data.code == 200) {
            if (!res.data.data || res.data.data.length == 0) {
              Toast("无课程列表数据！");
              this.ListShow=true;
              this.loading2=false;
            } else {
              self.disabled = true;
              this.ListShow=false;
              setTimeout(() => {
                if (res.data.data.length == 10) {
                  self.disabled = false;
                }
                self.List = res.data.data;
                  this.loading2=false;
              }, 200);
            }
          } else {
            if (res.data.code == -100) {
              localStorage.setItem("login_token", "");
              setTimeout(function() {
                window.location.href =
                  "http://weikt.apps.skylarkly.com/api/v1/Login/user_login";
                // window.location.href = "http://weikt.webuildus.com/api/v1/Login/user_login";
              }, 300);
            } else {
              // Toast(res.data.msg);
              this.loading2=false;
            }

          }
        })
        // if(get_id==1){
        //   this.http.post('/api/v1/Course/get_course_list',{
        //     p:this.page,l:10,t:1}).then((res)=>{
        //     this.disabled = true;
        //     setTimeout(() => {
        //       if(res.data.data.length==10){
        //         this.disabled = false;
        //       }
        //       this.List=res.data.data
        //     }, 200);
        //
        //   })
        // }if(get_id==2){
        //   this.http.post('/api/v1/Course/get_course_list',{p:this.page,l:10,t:2}).then((res)=>{
        //     this.disabled = true;
        //     setTimeout(() => {
        //       if(res.data.data.length==10){
        //         this.disabled = false;
        //       }
        //       this.List=res.data.data
        //     }, 200);
        //
        //   })
        // }else{
        //   this.http.post('/api/v1/Course/get_course_list',{
        //     p:this.page,l:10,t:get_id}).then((res)=>{
        //     this.disabled = true;
        //     setTimeout(() => {
        //       if(res.data.data.length==10){
        //         this.disabled = false;
        //       }
        //       this.List=res.data.data
        //     }, 200);
        //
        //   })
        // }
      },
      golist(num) {
        this.$router.push({
          path: 'listdetail',
          query: {id: num},

        })
      },
    }
  };
</script>

<style lang="stylus" type="text/stylus">
  @import "homeList.styl";
</style>
<style lang="less" type="text/less" scoped>
  .home-list {
    position: relative;
  }

  .home-list > img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .home-list > div {
    position: relative;
    z-index: 1;
  }
  .showList{
    display: block;
  }
  .hideList{
    display: none;
  }
</style>



