
<template>
  <div id="study" v-cloak v-loading="loading2"
       element-loading-text="Loading...">
    <div class="study-cot" v-for="(item,index) in studyList">
      <div class="study-video" v-if="item.media_type=='text'" style="width: 100%;">
        <img :src="videoSrc"/>
      </div>
      <div class="study-video" v-if="item.media_type=='video'">
        <video :poster="item.media_path"   webkit-playsinline="true" x-webkit-airplay="true"  playsinline="true"x5-video-player-type="h5"x5-video-player-fullscreen="true"width="100%" height="215px"preload="auto" controls="controls">
          <source :src="item.media_path" type="video/mp4"/>
          <source :src="item.media_path" type="video/webM"/>
          <source :src="item.media_path" type="video/ogg"/>
        </video>
      </div>
      <div class="study-video" v-if="item.media_type=='audio'" style="width: 100%;">
        <!--<audio :src="item.media_path" controls></audio>-->
          <!--<VueAudio :theUrl="audios[index].url" :theControlList="audios[index].controlList"/>-->
        <aplayer controls="controls" :music="{
            title: item.content,
            author: 'a',
            url: item.media_path,
            pic: item.media_path,
          }">
        </aplayer>
      </div>


      <div class="list-title">
        <!--<div class="list-china flex-container flex-jus-sb" v-if="item.media_type == 'audio' ? false : true">
          <div class="list-wh">
            <h2 class="font18">{{studyData.title}}</h2>
            <span class="iconfont icon-ren font12 color-99 flex-container flex-jus-s align-items-cen"><em
              class="padd-l-20">{{studyData.study_num}}人已学习</em></span>
          </div>
        </div>-->
        <div class="list-chapter">
          <div class="chapter-title font15 flex-container flex-jus-s"></div>
          <ul class="chapter-ul">
            <li class="chapter-ul-li">
              <!-- <div class="study-ya flex-container flex-jus-s font14 align-items-cen padd-b-10"><i class="study-yuan"></i><i class="padd-l-5">{{studyList.title}}</i></div> -->
              <div class="chapter-img text-left"   v-html="item.content">
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <van-tabbar v-model="active">
      <van-tabbar-item @click="geSt">
        <span>学习</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active : icon.normal"
        >
      </van-tabbar-item>
      <van-tabbar-item @click="goSo" v-show="exerciseList"><span>练习</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active1 : icon.normal1"
        >
      </van-tabbar-item>
      <van-tabbar-item @click="goTe" v-show="testList"><span>测验</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active2 : icon.normal2"
        >
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script type="text/javascript">
  import VueAudio from '@/components/VueAudio'
  import videoSrc from "@/assets/images/58cb99771007c.jpg";
  import posSrc from "@/assets/images/shipin_zanting.png";
  import study from "@/assets/images/xuexi_1.png";
  import study_active from "@/assets/images/xuexi.png";
  import practice from "@/assets/images/lianxi_1.png";
  import practice_active from "@/assets/images/lainxi.png";
  import test from "@/assets/images/ceyan_1.png";
  import test_active from "@/assets/images/ceyan.png";
  import zts from "@/assets/images/zanting_1.png";
  import paly from "@/assets/images/pause.jpg";
  import Vue from "vue";
  import Aplayer from 'vue-aplayer'

  // import "@/assets/css/audio.css";
  // import "@/assets/css/audio.js";
  import {Tabbar, TabbarItem, Toast} from "vant";
  Vue.use(Tabbar,TabbarItem,Toast);
  export default {
    name: "study",
    components: {
      Aplayer
    },
    data() {
      return {
        zts:zts,
        paly:paly,
        ztShow:false,
        active: 0,
        loading2:true,
        icon: {
          normal: study,
          active: study_active, //自定义图标1
          normal1: practice,
          active1: practice_active, //自定义图标2
          normal2: test,
          active2: test_active //自定义图标3
        },
        videoSrc: videoSrc, //video图片
        posSrc: posSrc,
        studyList: [],
        studyData: [],
        studyTitle:'',
        media_type: '',
        exerciseList: false,
        testList: false,
        audios: [],
        list3: [
        ],
      };
    },
    mounted: function () {
      this.getSid();
    },
    created() {
      this.getTest();
    },
    methods: {
      getSid() {
        var self = this;
        self.http
          .post("/api/v1/Course/get_chapter_info", {id: self.$route.query.id})
          .then(res => {
            if (res.data.code == 200) {
              if (!res.data.data || res.data.data.length == 0) {
                Toast("暂无数据！");
                this.loading2=false;
                return;
              }
              self.studyData = res.data.data;
              if (!res.data.data.content_list || res.data.data.content_list.length == 0) {
                Toast("暂无章节信息数据！");
              } else {
                self.studyList = res.data.data.content_list;
                for (var i = 0; i < self.studyList.length; i++) {
                  var json = {
                    url: self.studyList[i].media_path,
                    controlList:''
                  };
                  self.audios.push(json);
                }
                console.log( self.audios);

              }
            } else {
              if (res.data.code == -100) {
                localStorage.setItem("login_token", "");
                setTimeout(function () {
                  window.location.href =
                    "http://weikt.apps.skylarkly.com/api/v1/Login/user_login";
                  // window.location.href = "http://weikt.webuildus.com/api/v1/Login/user_login";
                }, 300);
              } else {
                Toast(res.data.msg);

              }
            }

            this.loading2=false;
          });
      },
      geSt() {
        if (!this.exerciseList && !this.testList) {
          Toast("你已经在当前页面了");
        }
      },
      goSo() {
        this.$router.push({
          path: "practice",
          query: {id: this.$route.query.id}
        });
      },
      goTe() {
        this.$router.push({
          path: "test",
          query: {id: this.$route.query.id}
        });
      },

     getTest() {
       var self = this;
        self.http
          .post("/api/v1/Course/get_chapter_test", {
            id: self.$route.query.id
          })
          .then(res => {
            if (res.data.code == 200) {
              if (res.data.data.length == 0 || !res.data.data) {
                // Toast("暂无数据！");
                return;
              }
              if (res.data.data.list) {
                self.testList = res.data.data.list.length == 0 ? false : true;
              }
            } else {
              // Toast(res.data.msg);
            }
          });
        self.http
          .post("/api/v1/Course/get_chapter_exercise", {
            id: self.$route.query.id
          })
          .then(res => {
            if (res.data.code == 200) {
              if (res.data.data.length == 0 || !res.data.data) {
                // Toast("暂无数据！");
                return;
              }
              self.exerciseList = res.data.data.length == 0 ? false : true;
            } else {
              if (res.data.code == -100) {
                localStorage.setItem("login_token", "");
                setTimeout(function () {
                  window.location.href =
                    "http://weikt.apps.skylarkly.com/api/v1/Login/user_login";
                  // window.location.href = "http://weikt.webuildus.com/api/v1/Login/user_login";
                }, 300);
              } else {
                // Toast(res.data.msg);
              }
            }
          });
      },
      playPause(){
        let myVideo=document.getElementById("video1");
        if (myVideo.paused)
          myVideo.play();
        else
          myVideo.pause();
      }
    }
  };




</script>

<style lang="stylus" type="text/stylus">
  @import 'study.styl';


</style>



