<template>
  <div id="app">
    <router-view/>

  </div>
</template>

<script>
export default {
  name: "App",
  mounted() {
  },
  methods: {}
};
</script>

<style lang="less">
.events-wrapper {
  display: none;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #333;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.clear {
  clear: both;
  content: " ";
  display: block;
}
.rank {
  .van-tab--active {
    color: #4fdaa9;
  }
  .van-tabs__line {
    background-color: #4fdaa9;
  }
}
</style>



