<template>
  <div id="homeList" v-cloak v-loading="loading2"
       element-loading-text="Loading...">
    <!-- <div class="home-list flex-container flex-jus-s align-items-start flex-direction-col color-ff" v-for="(item,index) in List" :style="{backgroundImage: 'url(' + avatar + ')', backgroundSize:'100% 100%',backgroundRepeat:'no-repeat'}" @click="golist(item.id
)"  v-waterfall-lower="loadMore"
       waterfall-disabled="disabled"
       waterfall-offset="400">
    <div class="font24 padd-t-5 text-left">{{item.title}}</div>
    <div class="font14 padd-t-10"><span class="home-line">{{item.classify_name}}</span>  <span class="padd-l-5">{{item.chapter_num}}章节</span></div>
  </div> -->
    <div class="card-wrap">
      <div class="card" v-for="(item,index) in List" :key="index"
           :style="{backgroundImage: 'url(' + avatar + ')', backgroundSize:'100% 100%',backgroundRepeat:'no-repeat'}"
           @click="golist(item.id
)" v-waterfall-lower="loadMore"
           waterfall-disabled="disabled"
           waterfall-offset="400">
        <h2>{{item.classify_name}}</h2>
        <p>{{item.chapter_num}}人正在学</p>
      </div>
    </div>
    <div v-if="List.length==0" class=" flex-container flex-jus-c align-items flex-direction-col height100vh">
      <div class="homeImg"><img :src="zwt"/></div>
      <p class="text-center font15 color-99">暂时还没有相关信息哦~</p>
    </div>
  </div>
</template>

<script>
  import Vue from "vue";
  import {Waterfall, Toast} from "vant";

  Vue.use(Waterfall).use(Toast);
  import avatar from "@/assets/images/cha.png";
  import zwt from "@/assets/images/wx.png";
  // import jiu from '@/assets/images/jiu.png'
  // import jieri from '@/assets/images/jieri.png'
  export default {
    directives: {
      WaterfallLower: Waterfall("lower"),
      WaterfallUpper: Waterfall("upper")
    },
    name: "homeList",

    data() {
      return {
        avatar: avatar,
        zwt: zwt,
        disabled: false,
        loading2:false,
        page: 1,
        num: 10,
        List: []
      };
    },
    mounted() {
      this.loadMore();
    },
    methods: {
      loadMore() {
        var self = this;
        this.http.post("/api/v1/Classify/get_classify_list", {
          p: this.page,
          l: 10,
          pi: this.$route.query.id
        }).then(res => {
          if (res.data.code == 200) {
            if (!res.data.data || res.data.data.length == 0) {
              Toast("无分类数据！");
            } else {
              self.disabled = true;
              setTimeout(() => {
                if (res.data.data.length == 10) {
                  self.disabled = false;
                }
                self.List = res.data.data;
              }, 200);
            }

          } else {
            if (res.data.code == -100) {
              localStorage.setItem("login_token", "");
              setTimeout(function() {
                window.location.href =
                  "http://weikt.apps.skylarkly.com/api/v1/Login/user_login";
                // window.location.href = "http://weikt.webuildus.com/api/v1/Login/user_login";
              }, 300);
            } else {
              Toast(res.data.msg);
            }
          }
          this.loading2=false;
        });
        // this.http
        //   .post("/api/v1/Classify/get_classify_list", {
        //     p: this.page,
        //     l: 10,
        //     pi: this.$route.query.id
        //   })
        //   .then(res => {});
        // if(get_id==1){
        //   this.http.post('/api/v1/Course/get_course_list',{
        //     p:this.page,l:10,t:1}).then((res)=>{
        //     this.disabled = true;
        //     setTimeout(() => {
        //       if(res.data.data.length==10){
        //         this.disabled = false;
        //       }
        //       this.List=res.data.data
        //     }, 200);
        //
        //   })
        // }if(get_id==2){
        //   this.http.post('/api/v1/Course/get_course_list',{p:this.page,l:10,t:2}).then((res)=>{
        //     this.disabled = true;
        //     setTimeout(() => {
        //       if(res.data.data.length==10){
        //         this.disabled = false;
        //       }
        //       this.List=res.data.data
        //     }, 200);
        //
        //   })
        // }else{
        //   this.http.post('/api/v1/Course/get_course_list',{
        //     p:this.page,l:10,t:get_id}).then((res)=>{
        //     this.disabled = true;
        //     setTimeout(() => {
        //       if(res.data.data.length==10){
        //         this.disabled = false;
        //       }
        //       this.List=res.data.data
        //     }, 200);
        //
        //   })
        // }
      },
      golist(num) {
        // this.$router.push({
        //   path: "listdetail",
        //   query: { id: num }
        // });
        this.$router.push({
          path: "homeList",
          query: {id: num}
        });
      }
    }
  };
</script>

<style lang="stylus" type="text/stylus">
  @import 'homeList.styl';
</style>
<style lang="less">
  .card {
    padding: 1.5rem 2rem;
    margin-top: 0.5rem;
    h2 {
      padding: 0;
      margin: 0;
      font-size: 0.9rem;
      font-family: PingFang SC;
      font-weight: Bold;
      color: rgba(255, 255, 255, 1);
      text-align: left;
    }
    p {
      padding: 0;
      margin: 0;
      font-size: 0.6rem;
      font-family: PingFang SC;
      color: rgba(255, 255, 255, 1);
      margin-top: 0.5rem;
      text-align: left;
    }
  }

  .card-wrap {
    padding: 0 0.5rem;
  }
</style>



