<template>
  <div id="test" v-cloak v-loading="loading2"
       element-loading-text="Loading...">
    <input type="file" id="luyin" style="display:none" accept="audio/*;capture=microphone">
    <div class="list-chapter" v-show="showType==2">
      <ul class="chapter-ul">
        <li class="chapter-ul-li" v-for="(item,indexa) in testList" :key="indexa">
          <div class="flex-container flex-jus-s align-items-start text-justify font15 padd-b-10">{{item.topic}}</div>
          <p class="flex-container flex-jus-s align-items-start flex-warp font14 line-height24"
             v-for="(i,kk) in item.option" :id="i.id" :key="kk" v-bind:class="{'yes' : item.check==i.id}"
             @click="checked(i,item)">{{i.option_str}}</p>

        </li>
      </ul>
      <div class="test-submit">
        <button class="text-sub font18" @click="submit">提交</button>
      </div>
    </div>
    <div class="yuyinC" v-show="showType==1">
      <div class="content" v-for="(item,index) in testList" :key="index">
        <h2>正文：</h2>
        <div class="white">
          <p v-html="item.topic"></p>
        </div>
      </div>
      <div class="yuyin" v-show="record==1">
        <div class="videoBg" @touchstart.prevent="TouchStart($event)" @touchend.prevent="TouchEnd($event)"></div>
        <!--<p>大声朗读，不断提升</p>-->
      </div>
      <div class="shiting" v-show="record==0">
        <van-row>
          <van-col span="8">
            <div @click="PlayClick">
              <img src="@/assets/images/icon_shiting.png" alt="">
              <p>试听</p>
            </div>
          </van-col>
          <van-col span="8">
            <div @click="ClearVideo">
              <img src="@/assets/images/chonglu.png" alt="">
              <p>重录</p>
            </div>
          </van-col>
          <van-col span="8">
            <div @click="uploadluyin">
              <img src="@/assets/images/sahngnchaun.png" alt="">
              <p>上传</p>
            </div>
          </van-col>
        </van-row>
      </div>
    </div>
    <div class="answer-parsing" v-if="answerParsing">
      <div v-if="!reviewResults.list" class=" flex-container flex-jus-c align-items flex-direction-col"
           :style="{height: '50vh'}">
        <div class="homeImg"><img :src="icon.test_success"/></div>
        <p class="text-center font15 color-99">恭喜你，全部答对了哦！</p>
      </div>
      <ul class="chapter-ul" v-if="reviewResults.list">
        <li class="chapter-ul-li">
          <div class="flex-container flex-jus-s align-items-start text-justify font15 padd-b-10">
            共{{reviewResults.total_topic}}题
          </div>
          <p class="flex-container flex-jus-s align-items-start flex-warp font14 line-height24">
            答对{{reviewResults.correct_topic}}题
          </p>
          <p class="flex-container flex-jus-s align-items-start flex-warp font14 line-height24">
            答错{{reviewResults.error_topic}}题
          </p>
        </li>
        <li class="chapter-ul-li" v-for="(item,indexb) in reviewResults.list" :key="indexb" v-if="item">
          <div class="flex-container flex-jus-s align-items-start text-justify font15 padd-b-10"
               :style="{color: '#ccc'}">答题解析
          </div>
          <div class="flex-container flex-jus-s align-items-start text-justify font15 padd-b-10">{{item.topic}}</div>
          <p class="flex-container flex-jus-s align-items-start flex-warp font14 line-height24"
             v-for="(i,ii) in item.child_options" :id="i.id" :key="ii" v-bind:class="{'yes' : i.my_answer==1}">
            {{i.option_str}}
          </p>
          <div class="flex-container flex-jus-s align-items-start text-justify font14 padd-b-10">
            正确答案:{{item.option_str}}
          </div>
          <p class="flex-container flex-jus-s align-items-start flex-warp font14 line-height24">
            {{item.currect_analyze}}
          </p>
        </li>
      </ul>
      <div class="test-submit">
        <button class="text-sub font18" @click="goBack">返回</button>
      </div>
    </div>
    <van-tabbar v-model="active">
      <van-tabbar-item @click.native="goSt">
        <span>学习</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active : icon.normal"
        >
      </van-tabbar-item>
      <van-tabbar-item @click="goSo" v-show="exerciseList"><span>练习</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active1 : icon.normal1"
        >
      </van-tabbar-item>
      <van-tabbar-item><span>测验</span>
        <img
          slot="icon"
          slot-scope="props"
          :src="props.active ? icon.active2 : icon.normal2"
        >
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import study from "@/assets/images/xuexi_1.png";
import study_active from "@/assets/images/xuexi.png";
import practice from "@/assets/images/lianxi_1.png";
import practice_active from "@/assets/images/lainxi.png";
import test from "@/assets/images/ceyan_1.png";
import test_active from "@/assets/images/ceyan.png";
import test_success from "@/assets/images/success.jpg";
import Vue from "vue";
import { Tabbar, TabbarItem, Cell, CellGroup } from "vant";

Vue.use(Tabbar).use(TabbarItem);
import { RadioGroup, Radio } from "vant";
import { Toast } from "vant";

Vue.use(Toast);
Vue.use(RadioGroup);
Vue.use(Radio);
Vue.use(Cell);
Vue.use(CellGroup);
const jsApi = [
  "checkJsApi",
  "onMenuShareTimeline",
  "onMenuShareAppMessage",
  "onMenuShareQQ",
  "onMenuShareWeibo",
  "onMenuShareQZone",
  "hideMenuItems",
  "showMenuItems",
  "hideAllNonBaseMenuItem",
  "showAllNonBaseMenuItem",
  "translateVoice",
  "startRecord",
  "stopRecord",
  "onVoiceRecordEnd",
  "playVoice",
  "onVoicePlayEnd",
  "pauseVoice",
  "stopVoice",
  "uploadVoice",
  "downloadVoice",
  "chooseImage",
  "previewImage",
  "uploadImage",
  "downloadImage",
  "getNetworkType",
  "openLocation",
  "getLocation",
  "hideOptionMenu",
  "showOptionMenu",
  "closeWindow",
  "scanQRCode",
  "chooseWXPay",
  "openProductSpecificView",
  "addCard",
  "chooseCard",
  "openCard"
];
export default {
  name: "test",
  data() {
    return {
      active: 2,
      showType: -1,
      icon: {
        normal: study,
        active: study_active, //自定义图标1
        normal1: practice,
        active1: practice_active, //自定义图标2
        normal2: test,
        active2: test_active, //自定义图标3
        test_success: test_success
      },
      loading2:true,
      testList: [],
      answer: [],
      START: 0,
      localId: "",
      record: 1,
      exerciseList: false,
      answerParsing: false,
      reviewResults: {
        total_topic: 0,
        correct_topic: 0,
        error_topic: 0,
        list: []
      }
    };
  },
  created() {
    this.getWx();
    document.oncontextmenu = function(e) {
      e.preventDefault();
    };
    this.getExercise();
  },
  mounted() {
    this.getTest();
  },
  methods: {
    getWx() {
      var _this = this;
      _this.http
        .get(
          "/api/v1/Common/generate_config?curUrl=" + location.href.split("#")[0]
        )
        .then(res => {
          const msg = res.data.data;
          this.loading2=false;
          if (msg) {
            _this.wx.config({
              debug: false,
              appId: msg.appId,
              timestamp: msg.timestamp,
              nonceStr: msg.nonceStr,
              signature: msg.signature,
              jsApiList: jsApi
            });
            _this.wx.ready(function() {
              _this.wx.checkJsApi({
                jsApiList: jsApi,
                success: function(res) {
                  // console.log(JSON.stringify(res));
                }
              });
            });
            _this.wx.error(function(err) {
              Toast("配置失败：" + JSON.stringify(err));
            });
          } else {
            Toast(res.data.msg);
            this.loading2=false;
          }
        });
    },
    ClearVideo() {
      this.localId = "";
      Toast("录音已经清空，请重新录制。");
      this.record = 1;
    },
    PlayClick() {
      var _this = this;
      _this.wx.ready(function() {
        if (_this.localId == "") {
          Toast("请先录制一段声音");
          return;
        }
        _this.wx.playVoice({
          localId: _this.localId
        });
      });
    },
    TouchStart(e) {
      e.preventDefault();
      var _this = this;
      _this.wx.ready(function() {
        _this.wx.startRecord({
          success: function() {
            Toast("录音开始");
            _this.START = new Date().getTime();
            _this.wx.onVoiceRecordEnd({
              // 录音时间超过一分钟没有停止的时候会执行 complete 回调
              complete: function(res) {
                Toast("最多只能录制一分钟");
                _this.localId = res.localId;
                // _this.uploadluyin(_this.localId);
              }
            });
          },
          fail: function(err) {
            Toast("录音结束:" + JSON.stringify(err));
          },
          cancel: function() {
            Toast("用户拒绝授权录音");
          }
        });
      });
    },
    TouchEnd(e) {
      e.preventDefault();
      var _this = this;
      let END = new Date().getTime();
      //录音时间
      let luyintime = END - this.START;
      if (luyintime < 2000) {
        END = 0;
        this.START = 0;
        _this.wx.stopRecord({});
        Toast("录音时间不能少于2秒");
        return false;
        //小于300ms，不录音
      } else {
        _this.wx.stopRecord({
          success: function(res) {
            _this.localId = res.localId;
            _this.record = 0;
            // 上传到服务器
            // _this.uploadluyin(_this.localId);
          },
          fail: function(res) {
            Toast(JSON.stringify(res));
          }
        });
      }
    },
    uploadluyin() {
      var _this = this;
      if (!_this.localId) {
        return;
      }
      _this.wx.uploadVoice({
        localId: _this.localId, // 需要上传的音频的本地ID，由stopRecord接口获得
        isShowProgressTips: 1, // 默认为1，显示进度提示
        success: function(res) {
          Toast("上传中");
          _this.http
            .post(
              "/api/v1/Course/audio_work_submit?cc_id=" + _this.$route.query.id,
              {
                files: res.serverId
              }
            )
            .then(res => {
              Toast(res.data.msg);
            });
          //把录音在微信服务器上的id（res.serverId）发送到自己的服务器供下载。
          // $.ajax({
          //     url: '后端处理上传录音的接口',
          //     type: 'post',
          //     data: JSON.stringify(res),
          //     dataType: "json",
          //     success: function (data) {
          //         alert('文件已经保存到自己的服务器');
          //     },
          //     error: function (xhr, errorType, error) {
          //         console.log(error);
          //     }
          // });
        }
      });
    },
    checked(a, b) {
      // for(var i=0;i<b.length;i++){
      //     b[i].check=false
      // }
      Vue.set(b, "check", a.id);
    },
    goSt() {
      this.$router.push({
        path: "study",
        query: { id: this.$route.query.id }
      });
    },
    goSo() {
      this.$router.push({
        path: "practice",
        query: { id: this.$route.query.id }
      });
    },
    getTest() {
      var _this = this;
      _this.http
        .post("/api/v1/Course/get_chapter_test", {
          id: _this.$route.query.id
        })
        .then(res => {
          _this.showType = res.data.data.test_type;
          _this.testList = res.data.data.list;
          this.loading2=false;
          if (_this.showType == 2) {
            for (var i = 0; i < _this.testList.length; i++) {
              Vue.set(_this.testList[i], "check", "");
            }
          } else {
          }
        });
    },
    submit() {
      var _this = this;
      _this.answer = {};
      for (var i = 0; i < _this.testList.length; i++) {
        if (_this.testList[i].check.length == 0) {
          Toast("您还没有完成全部测验~");
          return false;
        }
        _this.answer[_this.testList[i].id] = _this.testList[i].check;
      }

      _this.http
        .post("/api/v1/Course/slect_work_submit", {
          cc_id: _this.$route.query.id,
          result: _this.answer
        })
        .then(res => {
          if (res.data.code == 200) {
            Toast.success(res.data.msg);
            _this.answerParsing = true;
            _this.showType = -1;
            _this.reviewResults = res.data.data;
          } else {
            if (res.data.code == -100) {
              localStorage.setItem("login_token", "");
              setTimeout(function() {
                window.location.href =
                  "http://weikt.apps.skylarkly.com/api/v1/Login/user_login";
                // window.location.href = "http://weikt.webuildus.com/api/v1/Login/user_login";
              }, 300);
            } else {
              Toast(res.data.msg);
            }
          }
        });
    },
    goBack() {
      window.location.href = "/api/v1/Login/user_login";
    },
    getExercise() {
      var _this = this;
      _this.http
        .post("/api/v1/Course/get_chapter_exercise", {
          id: _this.$route.query.id
        })
        .then(res => {
          if (res.code == 404) {
          } else {
            _this.exerciseList = res.data.data.length == 0 ? false : true;
          }
          this.loading2=false;
        });
    }
  }
};
</script>

<style lang="stylus" type="text/stylus">
@import 'test.styl';
</style>
<style lang="less" type="text/less" scoped>
.videoBg {
  margin: 0 auto;
  width: 36px;
  height: 36px;
  background-image: url("../../assets/images/yuyin.png");
  background-size: cover;
}

.btn {
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-left: 14px;
  padding-right: 14px;
  font-size: 18px;
  text-align: center;
  text-decoration: none;
  overflow: visible;
  height: 42px;
  border-radius: 5px;
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  color: #ffffff;
  line-height: 42px;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  background-color: rebeccapurple;
}

.yes {
  color: #58dad8;
}

.van-tabbar-item--active {
  color: #4fdaa9;
}

.yuyinC {
  * {
    box-sizing: border-box;
  }
}

.shiting {
  width: 50%;
  margin: 0 auto;
  img {
    width: 0.98rem;
    height: 0.9rem;
  }
  p {
    font-size: 0.75rem;
    font-weight: normal;
    font-stretch: normal;
    letter-spacing: 0rem;
    color: #4fdaa9;
  }
}

.yuyin {
  img {
    height: 2.15rem;
  }
  width: 100%;
  text-align: center;
  margin-bottom: 0.73rem;

  p {
    font-family: PingFang-SC-Medium;
    font-size: 0.7rem;
    font-weight: normal;
    font-stretch: normal;
    letter-spacing: 0rem;
    color: #999999;
  }
}

.content {
  padding: 0.73rem;
  text-align: left;
  h2 {
    font-family: PingFang-SC-Bold;
    font-size: 0.75rem;
    font-weight: normal;
    font-stretch: normal;
    letter-spacing: 0rem;
    color: #000000;
  }
  .white {
    margin-top: 0.88rem;
    padding: 0.9rem 1.68rem 1.43rem 0.68rem;
    width: 100%;
    background-color: #ffffff;
    box-shadow: 0rem 0.25rem 0.5rem 0rem rgba(79, 218, 169, 0.1);
    border-radius: 0.5rem;
    p {
      font-family: PingFang-SC-Medium;
      font-size: 0.7rem;
      font-weight: normal;
      font-stretch: normal;
      color: #333333;
      line-height: 1.1rem;
      word-wrap: break-word;
    }
  }
}
</style>




